import Vue from 'vue';
import VueRouter from 'vue-router';

import PageMain from '../views/page-main.vue';
import PageDefault from '../views/page-default.vue';
import PageLocale from '../views/page-locale.vue';

Vue.use(VueRouter);

export default new VueRouter({
    routes: [
        {
            path: '/',
            redirect: '/main'
        },
        {
            path: '/main',
            component: PageMain,
            children: [
                {
                    path: '',
                    redirect: 'default'
                },
                {
                    path: 'default',
                    component: PageDefault
                },
                {
                    path: 'locale',
                    component: PageLocale
                }
            ]
        }
    ]
});